import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { MenuItemModel } from './menu-item.model';

@Component({
  selector: 'kkl-menu-item',
  template: (require('./menu-item.component.html').default || require('./menu-item.component.html')).length ? (require('./menu-item.component.html').default || require('./menu-item.component.html')) : '',
  styles: [(require('./menu-item.component.scss').default || require('./menu-item.component.scss')).length ? (require('./menu-item.component.scss').default || require('./menu-item.component.scss')) : '']
})
export class MenuItemComponent implements OnInit {

  @Input() public link: MenuItemModel;

  public active$: Observable<boolean>;

  @Output() click: EventEmitter<void> = new EventEmitter();

  constructor() {}

  ngOnInit(): void {
    this.active$ = this.link.getActiveObs();
  }

  public onLinkClick() {
    this.click.emit();
  }
}
